<template>
  <article>
    <iframe :src="source" frameborder="0"> </iframe>
    <div>
      <h3>
        <slot name="h3"></slot>
      </h3>
      <p>
        <slot name="about"></slot>
      </p>
      <button @click="showFrame($event)">take a live look &gt;</button>
    </div>
    <button @click="hideFrame($event)" class="hide">
      <i class="fas fa-arrow-left"></i>
    </button>
  </article>
</template>

<script>
export default {
  name: "PFrame",
  props: ["source"],
  methods: {
    showFrame(e) {
      e.currentTarget.parentElement.classList.add("go-left");
    },
    hideFrame(e) {
      e.currentTarget.previousSibling.classList.remove("go-left");
    },
  },
};
</script>

