<template>
  <transition name="pulse-button">
    <button v-if="path != '' || !isHeader" @click="updatePath">
      <router-link to="/">
        <i class="fas fa-arrow-left"></i>
      </router-link>
    </button>
  </transition>
</template>

<script>
export default {
  name: "PBack",
  computed: {
    path() {
      return this.$store.state.path;
    },
    isHeader() {
      return this.$store.state.isHeader;
    },
  },
  methods: {
    updatePath() {
      this.$store.state.isHeader = true;
      this.$store.commit("updatePath");
    },
  },
};
</script>

<style lang="scss" scoped>
button {
  line-height: 12px;
  z-index: 3;
  position: fixed;
  top: 50px;
  left: 20px;
  border-radius: 50%;
  a {
    color: var(--main);
  }
}

.pulse-button-enter-from,
.pulse-button-leave-to {
  transform: translateY(-10px);
  opacity: 0%;
}

.pulse-button-enter-active,
.pulse-button-leave-active {
  transition: all 0.6s ease-in-out;
}
</style>
