import { createStore } from "vuex";

export default createStore({
  state: {
    path: location.href.slice(location.href.lastIndexOf("/") + 1),
    isHeader: true,
  },
  mutations: {
    updatePath() {
      this.state.path = location.href.slice(location.href.lastIndexOf("/") + 1);
    },
    removeHeader() {
      this.state.isHeader = false;
    },
  },
  actions: {},
  modules: {},
});
