<template>
  <div class="loading">
    <div id="blop">
      <img src="../assets/logo.png" alt="" />
    </div>
    <div class="the-des">
      <h2>Moneer Al-Dakkak</h2>
      <h3>Full stack web developer</h3>
      <h3>
        <i class="fab fa-vuejs fa-2x"></i> <i class="fab fa-node-js fa-2x"></i>
      </h3>
    </div>
  </div>
</template>

<script>
export default {
  name: "PLoading",
  methods: {
    create() {
      var r1 = this.random(25, 75);
      var r2 = this.random(25, 75);
      var r3 = this.random(25, 75);
      var r4 = this.random(25, 75);

      var r11 = this.remain(r1);
      var r22 = this.remain(r2);
      var r33 = this.remain(r3);
      var r44 = this.remain(r4);

      document.getElementById(
        "blop"
      ).style.borderRadius = `${r1}% ${r11}% ${r22}% ${r2}% / ${r3}% ${r4}% ${r44}% ${r33}%`;
    },
    random(min, max) {
      return Math.floor(min + Math.random() * (max - min));
    },

    remain(n) {
      return 100 - n;
    },
  },
  mounted() {
    this.int = setInterval(this.create, 600);
  },
  unmounted() {
    clearInterval(this.int);
  },
};
</script>

